<template>
    <div class="books">
        <h2 class="title">Books</h2>
        <div class="container">
            <div class="card card--books" v-for="book in books" :key="book.id">
                <div class="card-content">
                    <router-link :to="{ name: 'EditBook', params: { book_slug: book.slug } }">
                        <i class="edit far fa-edit"></i>
                    </router-link>
                    <a href="" @click.prevent="deleteBook(book.id)"><i class="delete far fa-trash-alt"></i></a>
                    <h2>{{ book.title }}</h2>
                    <p>{{ book.writer }}</p>
                </div>
            </div>
            <div class="card card--special card--books">
                <router-link :to="{ name: 'AddBook' }">
                    <div class="card-content">
                        <i class="add fas fa-plus"></i>
                    </div>
                </router-link>
            </div>
        </div>
    </div>
</template>

<script>
import db from '@/firebase/init'

export default {
    name: 'Books',
    data() {
        return {
            books: []
        }
    },
    methods: {
        deleteBook(id) {
            db.collection('books').doc(id).delete()
                .then(() => {
                    this.books = this.books.filter(book => {
                    return book.id != id
                    })
                 })     
            }
    },
    created() {
        db.collection('books').get()
        .then(snapshot => {
            snapshot.forEach(doc => {
                let book = doc.data()
                book.id = doc.id
                this.books.push(book);
            })
        })
    }
}
</script>

<style scoped>
    .card--books:hover {
        background-color: blueviolet;
    }

</style>



