<template>
    <div v-if="book" class="edit-book">
        <h2>Edit {{ book.title }} book </h2>
         <form @submit.prevent="EditBook">
            <div class="field edit-book-title">
                <label for="title">Book Title:</label>
                <input type="text" name="title" v-model="book.title">
            </div>
            <div class="field edit-book-writer">
                <label for="title">Book Writer:</label>
                <input type="text" name="writer" v-model="book.writer">
            </div>
            <div class="field">
                <p v-if="feedback" class="red-text">{{ feedback }}</p>
                <button class="btn red">Update book</button>
            </div>
        </form>
    </div>
</template>

<script>
import db from '@/firebase/init'
import * as firebase from 'firebase'

import slugify from 'slugify';
import axios from 'axios';

export default {
    name: 'EditBook',
    data() {
        return {
            book: null,
            another: null,
            feedback: null
        }
    },
    methods: {
        EditBook() {
             if(this.book.title) {
                this.feedback = null
                // create slug 
               this.book.slug = slugify(this.book.title, {
                    replacement: '-',
                    remove: /[$*_+~.()'"!\-:@]/g,
                    lower: true 
                })
                db.collection('books').doc(this.book.id).update({
                    title: this.book.title,
                    writer: this.book.writer,
                    slug: this.book.slug
                }).then(() => {
                    this.$router.push({ name: 'Books' })
                }).catch(err => {
                    console.log(err)
                })

            } else {
                this.feedback = 'You must enter a value'
            }
        },
         
    },
    created() {
        let ref = db.collection('books').where('slug', '==', this.$route.params.book_slug)
        ref.get().then(snapshot => {
            snapshot.forEach(doc => {
                this.book = doc.data()
                this.book.id = doc.id 
            })
        })
    }
}
</script>

<style>
.edit-book {
    text-align: center;
    margin-top: 200px;
}
 @media only screen and (max-width: 900px) {
     .edit-book {
         margin-top: 50px;
     }
 }

</style>



