<template>
    <div class="nav-container">
        <nav>
            <ul class="nav-ul">
                <li class="home">
                    <router-link :to="{ name: 'Index' }">
                        <a href="">Home</a>
                    </router-link>
                </li>
                <li class="main-title">My Collection Of Things</li>
                <li>
                    <a class="list-link" href=""><router-link :to="{ name: 'Books' }">Books</router-link></a>
                    <a class="list-link" href=""><router-link :to="{ name: 'Recipes' }">Recipes</router-link></a>
                    <a class="list-link" href=""><router-link :to="{ name: 'Wardrobe' }">Wardrobe</router-link></a>
                </li>
            </ul>
        </nav>
    </div>
</template>

<script>
export default {
    name: 'Navbar'
}
</script>

<style scoped>
    .nav-container {
        margin-bottom: 100px;
        padding-top: 20px;
    }
    .home {
        padding-left: 20px;
        font-size: 20px;
    }
    .nav-ul {
        padding: 0;
        display: flex;
        justify-content: space-between;
    }
    .nav-ul li {
        list-style: none;
    }
    .list-link {
        display: block;
        padding-right: 20px;
        font-size: 20px;
    }
    .main-title {
        font-size: 50px;
        text-align: center;
    }
    @media only screen and (max-width: 1200px) {
        .main-title {
            font-size: 35px;
            margin-bottom: 20px;
        }

        .list-link, .home {
            font-size: 15px;
            padding: 0;
            text-align: center;
            margin-bottom: 20px;
        }
         .nav-ul {
            flex-direction: column;
            justify-content: center;
            align-items: center;
        }
    }
    
</style>



