<template>
    <div class="add-item">
        <h2 class="title">Add New Item</h2>
        <form @submit.prevent="AddItem">
            <div class="field add-item-title">
                <label for="title">Item Title:</label>
                <input type="text" name="title" v-model="title">
            </div>
            <div v-for="(col, index) in colors" :key="index" class="field">
                <label for="colors">Colors:</label>
                <input type="text" name="colors" v-model="colors[index]">
                <a class="delete-ing" @click.prevent="deleteCol(col)" href=""><i class="far fa-trash-alt"></i></a>
            </div>
             <div class="field add-color">
                <label for="add-color">Add color: </label>
                <input type="text" name="add-color" @keydown.tab.prevent="addColor" v-model="another">
            </div>
            <div class="field">
                <p v-if="feedback" class="red-text">{{ feedback }}</p>
                <button class="btn">Add Item</button>
            </div>
        </form>
    </div>
</template>

<script>
import db from '@/firebase/init'
import slugify from 'slugify'

export default {
    name: 'AddItem',
    data() {
        return {
            title: null,
            another: null,
            colors: [],
            feedback: null,
            slug: null
        }
    },
    methods: {
        AddItem() {
            if(this.title) {
                this.feedback = null
                // create slug 
               this.slug = slugify(this.title, {
                    replacement: '-',
                    remove: /[$*_+~.()'"!\-:@]/g,
                    lower: true 
                })
                db.collection('items').add({
                    title: this.title,
                    colors: this.colors,
                    slug: this.slug
                }).then(() => {
                    this.$router.push({ name: 'Wardrobe' })
                }).catch(err => {
                    console.log(err)
                })

            } else {
                this.feedback = 'You must enter a item title'
            }
        },
         addColor() {
            if(this.another) {
                this.colors.push(this.another)
                this.another = null
                this.feedback = null
            } else {
                this.feedback = 'You must enter a value to add color'
            }
        },
        deleteCol(col) {
            this.colors = this.colors.filter(color => {
                return color != col
            })
        }
    }
}
</script>

<style scoped>

.add-item {
    text-align: center;
    margin-top: 200px;
}


</style>



