<template>
  <div class="index">
    <a class="list-link" href="">
      <router-link :to="{ name: 'Books' }">
        <div class="index-card">
          <img class="img" src="../assets/books.jpg" alt="">
          <div class="description">
            <h2 class="card-title">Books</h2>
            <p>As an intellectual object, a book is prototypically a composition of such great length that it takes a considerable investment of time to compose and a still considerable, though not so extensive, investment of time to read. </p>
          </div>
        </div>
      </router-link>
    </a>
    
    <a class="list-link" href="">
      <router-link :to="{ name: 'Recipes' }">
        <div class="index-card">
          <img class="img" src="../assets/recipes.png" alt="">
          <div class="description">
            <h2 class="card-title">Recipes</h2>
            <p>Here you can find a list of ingredients that are needed for a different kind of recipes.</p>
          </div>
        </div>
      </router-link>
    </a>

    <a class="list-link" href="">
      <router-link :to="{ name: 'Wardrobe' }">
        <div class="index-card">
          <img class="img" src="../assets/wardrobe.png" alt="">
          <div class="description">
            <h2 class="card-title">Wardrobe</h2>
            <p>A list of pieces of clothing. Clothing has been made from a very wide variety of materials, ranging from leather and furs to woven fabrics to elaborate and exotic natural and synthetic fabrics.</p>
          </div>
        </div>
      </router-link>
    </a>
  </div>
</template>

<script>
export default {
  name: 'Index',
  data () {
    return {
      
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .index {
    padding: 0 500px;
  }
  .img {
    max-width: 200px;
  }
  .index-card {
    margin-top: 70px;
    display: flex;
    align-items: flex-start;
  }
  .description {
    margin-left: 30px;
  }
  .card-title {
    padding-top: 0;
  }
  @media only screen and (max-width: 1400px) {
     .index {
        padding: 0 400px;
      }
  }
  @media only screen and (max-width: 1200px) {
    .index-card {
      padding: 0;
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 100%;
      text-align: center;
    }
    .card-title {
    padding-top: 70px;
  }
    .index {
      padding: 0 300px;
  }
  .description {
    margin-left: 0;
    width: 50%;
   }
  }
  @media only screen and (max-width: 900px) {
     
    .description {
      width: 90%;
    }
  }

</style>



