<template>
    <div class="add-item">
        <h2 class="title">Add New Recipe</h2>
        <form @submit.prevent="AddRecipe">
            <div class="field add-recipe-title">
                <label for="title">Recipe Title:</label>
                <input type="text" name="title" v-model="title">
            </div>
            <div v-for="(ing, index) in ingredients" :key="index" class="field">
                <label for="ingredient">Ingredient:</label>
                <input type="text" name="ingredient" v-model="ingredients[index]">
                 <a class="delete-ing" @click.prevent="deleteIng(ing)" href=""><i class="far fa-trash-alt"></i></a>
            </div>
             <div class="field add-ingredient">
                <label for="add-ingredient">Add an ingredient</label>
                <input type="text" name="add-ingredient" @keydown.tab.prevent="addIng" v-model="another">
            </div>
            <div class="field">
                <p v-if="feedback" class="red-text">{{ feedback }}</p>
                <button class="btn">Add Recipe</button>
            </div>
        </form>
    </div>
</template>

<script>
import db from '@/firebase/init'
import slugify from 'slugify'

export default {
    name: 'AddRecipe',
    data() {
        return {
            title: null,
            another: null,
            ingredients: [],
            feedback: null,
            slug: null
        }
    },
    methods: {
        AddRecipe() {
            if(this.title) {
                this.feedback = null
                // create slug 
               this.slug = slugify(this.title, {
                    replacement: '-',
                    remove: /[$*_+~.()'"!\-:@]/g,
                    lower: true 
                })
                db.collection('recipes').add({
                    title: this.title,
                    ingredients: this.ingredients,
                    slug: this.slug
                }).then(() => {
                    this.$router.push({ name: 'Recipes' })
                }).catch(err => {
                    console.log(err)
                })

            } else {
                this.feedback = 'You must enter a recipe title'
            }
        },
         addIng() {
            if(this.another) {
                this.ingredients.push(this.another)
                this.another = null
                this.feedback = null
            } else {
                this.feedback = 'You must enter a value to add an ingredient'
            }
        },
        deleteIng(ing) {
            this.ingredients = this.ingredients.filter(ingredient => {
                return ingredient != ing 
            })
        }
    }
}
</script>

<style scoped>

.add-item {
    text-align: center;
    margin-top: 200px;
}


</style>



