<template>
    <div class="wardrobe">
        <h2 class="title">Wardrobe</h2>
        <div class="container">
            <div class="card card--wardrobe" v-for="item in items" :key="item.id">
                <div class="card-content">
                    <router-link :to="{ name: 'EditItem', params: { item_slug: item.slug } }">
                        <i class="edit far fa-edit"></i>
                    </router-link>
                    <a href="" @click.prevent="deleteItem(item.id)"><i class="delete far fa-trash-alt"></i></a>
                    <h2>{{ item.title }}</h2>
                    <ul class="item-ul">
                        <li class="item" v-for="(color, index) in item.colors" :key="index">
                            {{ color }}
                        </li>
                    </ul>
                </div>
            </div>
            <div class="card card--special card--wardrobe">
                <router-link :to="{ name: 'AddItem' }">
                    <div class="card-content">
                        <i class="add fas fa-plus"></i>
                    </div>
                </router-link>
            </div>
        </div>
    </div>
</template>

<script>
import db from '@/firebase/init'

export default {
    name: 'Wardrobe',
    data() {
        return {
            items: []
        }
    },
    methods: {
        deleteItem(id) {
            db.collection('items').doc(id).delete()
                .then(() => {
                    this.items = this.items.filter(item => {
                    return item.id != id
                    })
                 })     
            }
    },
    created() {
        db.collection('items').get()
        .then(snapshot => {
            snapshot.forEach(doc => {
                let item = doc.data()
                item.id = doc.id
                this.items.push(item);
            })
        })
    }
}
</script>

<style scoped>
    .item {
        margin-left: 30px;
    }
   .item-ul {
       padding-bottom: 40px;
   }
    .card--wardrobe:hover {
        background-color:cadetblue;
    }
</style>



