<template>
    <div v-if="item" class="edit-item">
        <h2>Edit {{ item.title }} item </h2>
         <form @submit.prevent="EditItem">
            <div class="field edit-item-title">
                <label for="title">Item Title:</label>
                <input type="text" name="title" v-model="item.title">
            </div>
             <div v-for="(col, index) in item.colors" :key="index" class="field">
                <label for="color">Color:</label>
                <input type="text" name="color" v-model="item.colors[index]">
                <a @click.prevent="deleteCol(col)" href=""><i class="delete-color far fa-trash-alt"></i></a>
            </div>
            <div class="field add-color">
                <label for="add-color">Add color</label>
                <input type="text" name="add-color" @keydown.tab.prevent="addColor" v-model="another">
            </div>
            <div class="field">
                <p v-if="feedback" class="red-text">{{ feedback }}</p>
                <button class="btn red">Update item</button>
            </div>
        </form>
    </div>
</template>

<script>
import db from '@/firebase/init'
import * as firebase from 'firebase'

import slugify from 'slugify';
import axios from 'axios';

export default {
    name: 'EditItem',
    data() {
        return {
            item: null,
            another: null,
            feedback: null
        }
    },
    methods: {
        EditItem() {
             if(this.item.title) {
                this.feedback = null
                // create slug 
               this.item.slug = slugify(this.item.title, {
                    replacement: '-',
                    remove: /[$*_+~.()'"!\-:@]/g,
                    lower: true 
                })
                db.collection('items').doc(this.item.id).update({
                    title: this.item.title,
                    colors: this.item.colors,
                    slug: this.item.slug
                }).then(() => {
                    this.$router.push({ name: 'Wardrobe' })
                }).catch(err => {
                    console.log(err)
                })

            } else {
                this.feedback = 'You must enter a value'
            }
        },
        addColor() {
            if(this.another) {
                this.item.colors.push(this.another)
                this.another = null
                this.feedback = null
            } else {
                this.feedback = 'You must enter a value to add color'
            }
        },
        deleteCol(col) {
            this.colors.color = this.item.colors.filter(color => {
                return color != col 
            })
        },
         
    },
    created() {
        let ref = db.collection('items').where('slug', '==', this.$route.params.item_slug)
        ref.get().then(snapshot => {
            snapshot.forEach(doc => {
                this.item = doc.data()
                this.item.id = doc.id 
            })
        })
    }
}
</script>

<style>
.edit-item {
    text-align: center;
    margin-top: 200px;
}
@media only screen and (max-width: 900px) {
     .edit-item {
         margin-top: 50px;
     }
 }

</style>



