<template>
    <div v-if="recipe" class="edit-recipe">
        <h2>Edit {{ recipe.title }} recipe </h2>
         <form @submit.prevent="EditRecipe">
            <div class="field edit-recipe-title">
                <label for="title">Recipe Title:</label>
                <input type="text" name="title" v-model="recipe.title">
            </div>
             <div v-for="(ing, index) in recipe.ingredients" :key="index" class="field">
                <label for="ingredient">Ingredient:</label>
                <input type="text" name="ingredient" v-model="recipe.ingredients[index]">
                <a class="delete-ing" @click="deleteIng(ing)" href=""><i class="far fa-trash-alt"></i></a>
            </div>
            <div class="field add-ingredient">
                <label for="add-ingredient">Add an ingredient</label>
                <input type="text" name="add-ingredient" @keydown.tab.prevent="addIng" v-model="another">
            </div>
            <div class="field">
                <p v-if="feedback" class="red-text">{{ feedback }}</p>
                <button class="btn">Update recipe</button>
            </div>
        </form>
    </div>
</template>

<script>
import db from '@/firebase/init'
import * as firebase from 'firebase'

import slugify from 'slugify';
import axios from 'axios';

export default {
    name: 'EditRecipe',
    data() {
        return {
            recipe: null,
            another: null,
            feedback: null
        }
    },
    methods: {
        EditRecipe() {
             if(this.recipe.title) {
                this.feedback = null
                // create slug 
               this.recipe.slug = slugify(this.recipe.title, {
                    replacement: '-',
                    remove: /[$*_+~.()'"!\-:@]/g,
                    lower: true 
                })
                db.collection('recipes').doc(this.recipe.id).update({
                    title: this.recipe.title,
                    ingredients: this.recipe.ingredients,
                    slug: this.recipe.slug
                }).then(() => {
                    this.$router.push({ name: 'Recipes' })
                }).catch(err => {
                    console.log(err)
                })

            } else {
                this.feedback = 'You must enter a value'
            }
        },
        addIng() {
            if(this.another) {
                this.recipe.ingredients.push(this.another)
                this.another = null
                this.feedback = null
            } else {
                this.feedback = 'You must enter a value to add an ingredient'
            }
        },
        deleteIng(ing) {
            this.recipe.ingredients = this.recipe.ingredients.filter(ingredient => {
                return ingredient != ing 
            })
        },
         
    },
    created() {
        let ref = db.collection('recipes').where('slug', '==', this.$route.params.recipe_slug)
        ref.get().then(snapshot => {
            snapshot.forEach(doc => {
                this.recipe = doc.data()
                this.recipe.id = doc.id 
            })
        })
    }
}
</script>

<style>
.edit-recipe {
    text-align: center;
    margin-top: 200px;
}
@media only screen and (max-width: 900px) {
     .edit-recipe {
         margin-top: 50px;
     }
 }

</style>



