<template>
  <div id="app">
    <Navbar />
    <router-view/>
  </div>
</template>

<script>
import Navbar from './components/Navbar'

export default {
  name: 'App',
  components: {
    Navbar
  }
}
</script>

<style>
    *{
      margin: 0;
      padding: 0;
    }
    textarea:focus, input:focus{
    outline: none;
   }
    a {
      text-decoration: none;
      color: #000;
    }
    a:visited, a:hover, a:active {
      color: #000;
    }
    html {
      font-family: 'Pathway Gothic One', sans-serif;
    }

    .title {
    text-align: center;
    font-size: 40px;
  }
    .container {
        padding: 0 250px;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        grid-gap: 30px;
        margin-top: 50px;
    }
    .card {
       background-color: #fff;
       border: 1px solid gray;
       position: relative;
    }
    
    .delete {
        position: absolute;
        top: 5px;
        right: 30px;
        cursor: pointer;
    }
    .edit {
        position: absolute;
        top: 5px;
        right: 5px;
        cursor: pointer;
    }
    h2 {
        font-size: 25px;
        margin-left: 10px;
        padding: 20px;
    } 
    p {
        margin-left: 10px;
        padding: 20px;
    }
    .card--special {
        text-align: center;
        padding: 45px 0;
    }
    .add {
        font-size: 50px;
        display: inline;
    }
    .add-item {
        text-align: center;
        margin-top: 200px;
    }
    input[type=text] {
      width: 100%;
      padding: 12px 20px;
      margin: 8px 0;
      box-sizing: border-box;
      border: none;
      border-bottom: 2px solid #000;
    }
    .field {
        position: relative;
        width: 40%;
        margin: 10px auto;
    }
    .red-text {
        color: red;   
    }
    .btn {
        background-color: #fff;
        color: #000;
        padding: 20px;
        border: 1px solid #000;
        margin-top: 50px;
    }
    .delete-ing, .delete-color {
        position: absolute;
        right: 0;
        bottom: 16px;
    }
    @media only screen and (max-width: 1200px) {
            
            .container {
                display: flex;
                flex-direction: column;
                padding: 0 100px;
            }
            .card, .card--special {
                width: 50%;
                padding-bottom: 20px;
                margin: 20px auto;
            }
        }
    @media only screen and (max-width: 500px) {
             .container {
                padding: 0 50px;
            }
            .card, .card--special {
                width: 100%;
                margin: 20px auto;
            }
        }
   
</style>



