<template>
    <div class="add-item">
        <h2 class="title">Add New Book</h2>
        <form @submit.prevent="AddBook">
            <div class="field add-book-title">
                <label for="title">Book Title:</label>
                <input type="text" name="title" v-model="title">
            </div>
             <div class="field add-book-writer">
                <label for="title">Book Writer:</label>
                <input type="text" name="writer" v-model="writer">
            </div>
            <div class="field">
                <p v-if="feedback" class="red-text">{{ feedback }}</p>
                <button class="btn">Add Book</button>
            </div>
        </form>
    </div>
</template>

<script>
import db from '@/firebase/init'
import slugify from 'slugify'

export default {
    name: 'AddBook',
    data() {
        return {
            title: null,
            writer: null,
            another: null,
            feedback: null,
            slug: null
        }
    },
    methods: {
        AddBook() {
            if(this.title) {
                this.feedback = null
                // create slug 
               this.slug = slugify(this.title, {
                    replacement: '-',
                    remove: /[$*_+~.()'"!\-:@]/g,
                    lower: true 
                })
                db.collection('books').add({
                    title: this.title,
                    writer: this.writer,
                    slug: this.slug
                }).then(() => {
                    this.$router.push({ name: 'Books' })
                }).catch(err => {
                    console.log(err)
                })

            } else {
                this.feedback = 'You must enter a book title'
            }
        }
    }
}
</script>

<style scoped>




</style>



