<template>
    <div class="recipes">
        <h2 class="title">Recipes</h2>
       <div class="container">
            <div class="card card--recipes" v-for="recipe in recipes" :key="recipe.id">
                <div class="card-content">
                    <router-link :to="{ name: 'EditRecipe', params: { recipe_slug: recipe.slug } }">
                        <i class="edit far fa-edit"></i>
                    </router-link>
                    <a href="" @click.prevent="deleteRecipe(recipe.id)"><i class="delete far fa-trash-alt"></i></a>
                    <h2>{{ recipe.title }}</h2>
                    <ul class="recipes-ul">
                        <li class="ing" v-for="(ing, index) in recipe.ingredients" :key="index">
                            {{ ing }}
                        </li>
                    </ul>
                </div>
            </div>
            <div class="card card--special card--recipes">
                <router-link :to="{ name: 'AddRecipe' }">
                    <div class="card-content">
                        <i class="add fas fa-plus"></i>
                    </div>
                </router-link>
            </div>
       </div>
    </div>
</template>

<script>
import db from '@/firebase/init'

export default {
    name: 'Recipes',
    data() {
        return {
            recipes: []
        }
    },
    methods: {
        deleteRecipe(id) {
            db.collection('recipes').doc(id).delete()
                .then(() => {
                    this.recipes = this.recipes.filter(recipe => {
                    return recipe.id != id
                    })
                 })     
            }
    },
    created() {
        db.collection('recipes').get()
        .then(snapshot => {
            snapshot.forEach(doc => {
                let recipe = doc.data()
                recipe.id = doc.id
                this.recipes.push(recipe);
            })
        })
    }
}
</script>

<style scoped>
     .ing {
        margin-left: 30px;
    }
    .recipes-ul {
        padding-bottom: 20px;
    }
    .card--recipes:hover {
        background-color: blue;
    }
</style>



